import React, { useEffect } from "react";

import "./WordpressWebsiteDesign.css";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import wordPressBanner from "./wordpressBanner.jpg";

import WordPressForBusiness from "../../../components/word_press_website_design_components/wordpress_for_business/WordPressForBusiness";

import WordPressCardsLeft from "../../../components/word_press_website_design_components/wordpress_cards/WordPressCardsLeft";

import WordPressCardsRight from "../../../components/word_press_website_design_components/wordpress_cards/WordPressCardsRight";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png";
import pic2 from "./pic2.png";
import pic3 from "./pic3.png";
import pic4 from "./pic4.png";

import { useLocation } from "react-router-dom";

export default function WordpressWebsiteDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="wordpress-website-design-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="WordPress Website Design"
        bannerParagraph="Enhance sales through customized website services. Increase turnovers via hyper-personalized web solutions."
        bannerButton1="LET’S CHAT"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={wordPressBanner}
      />

      <WordPressForBusiness />

      <h2 className="wordpress-website-design-main-container-heading-2">
        WordPress is an excellent choice for businesses seeking an efficient and
        customizable online presence
      </h2>

      <WordPressCardsLeft
        heading="Scalability And Flexibility"
        paragrapgh="Allowing users to create websites with robust designs tailored to their needs. WordPress also enables them to easily modify the appearance of pages through plugins - no coding required! In addition, one can add other features such as image galleries or video player components quickly."
        button="LET’S CHAT"
        picture={pic1}
      />

      <WordPressCardsRight
        heading="SEO-ready WordPress Web design"
        paragraph="An effective WordPress site must be optimized for search engine optimization (SEO). Proper SEO involves optimizing the content and structure of a website to maximize its ranking within relevant online searches. It can also include building high-quality backlinks, creating metadata that accurately represents the page’s content, and deploying social media marketing strategies to further improve visibility on major search engines such as Google and Bing."
        button="LET’S CHAT"
        picture={pic2}
      />

      <WordPressCardsLeft
        heading="User-friendly content management solutions"
        paragrapgh="It enables users to effortlessly update their web pages without requiring coding expertise. It simplifies web page customization, allowing individuals to tailor their sites to specific needs, resulting in professional-looking designs at an affordable price"
        button="LET’S CHAT"
        picture={pic3}
      />

      <WordPressCardsRight
        heading="Powerful plugins to enhance the functionality of your website"
        paragraph="WordPress offers a wide range of powerful plugins to enhance the functionality of your website and to enhance your website's features and performance with ease. In short, investing in quality WordPress Website Design services will yield measurable results by improving a digital presence with lasting effects that all businesses need today in this interconnected landscape."
        button="LET’S CHAT"
        picture={pic4}
      />

      <br />
      <br />
      <br />
      <br />
      <Pricing />
      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />
      <br />

      <Ourwork />

      <Footer />
    </div>
  );
}
